import { N2WordsRU } from './ru.js';

export class N2WordsPL extends N2WordsRU {
  ones = {
    1: 'jeden',
    2: 'dwa',
    3: 'trzy',
    4: 'cztery',
    5: 'pi',
    6: 'sze',
    7: 'siedem',
    8: 'osiem',
    9: 'dziewi'
  };

  tens = {
    0: 'dziesi',
    1: 'jedenacie',
    2: 'dwanacie',
    3: 'trzynacie',
    4: 'czternacie',
    5: 'pitnacie',
    6: 'szesnacie',
    7: 'siedemnacie',
    8: 'osiemnacie',
    9: 'dziewitnacie'
  };

  twenties = {
    2: 'dwadziecia',
    3: 'trzydzieci',
    4: 'czterdzieci',
    5: 'pidziesit',
    6: 'szedziesit',
    7: 'siedemdziesit',
    8: 'osiemdziesit',
    9: 'dziewidzisit'
  };

  hundreds = {
    1: 'sto',
    2: 'dwiecie',
    3: 'trzysta',
    4: 'czterysta',
    5: 'piset',
    6: 'szeset',
    7: 'siedemset',
    8: 'osiemset',
    9: 'dziewiset'
  };

  thousands = {
    1: ['tysic', 'tysice', 'tysicy'], // 10^ 3
    2: ['milion', 'miliony', 'milionw'], // 10^ 6
    3: ['miliard', 'miliardy', 'miliardw'], // 10^ 9
    4: ['bilion', 'biliony', 'bilionw'], // 10^ 12
    5: ['biliard', 'biliardy', 'biliardw'], // 10^ 15
    6: ['trylion', 'tryliony', 'trylionw'], // 10^ 18
    7: ['tryliard', 'tryliardy', 'tryliardw'], // 10^ 21
    8: ['kwadrylion', 'kwadryliony', 'kwadrylionw'], // 10^ 24
    9: ['kwaryliard', 'kwadryliardy', 'kwadryliardw'], // 10^ 27
    10: ['kwintylion', 'kwintyliony', 'kwintylionw'], // 10^ 30
  };

  constructor(options) {
    options = Object.assign({
      negativeWord: 'minus',
      separatorWord: 'przecinek',
      zero: 'zero'
    }, options);

    super(options);
  }

  pluralize(n, forms) {
    let form = 2;
    if (n == 1) {
      form = 0;
    } else if ((n % 10n < 5n) && (n % 10n > 1n) && ((n % 100n < 10n) || (n % 100n > 20n))) {
      form = 1;
    }
    return forms[form];
  }

  toCardinal(number) {
    if (number == 0) {
      return this.zero;
    }
    const words = [];
    const chunks = this.splitByX(number.toString(), 3);
    let index = chunks.length;
    for (const x of chunks) {
      index = index - 1;
      if (x == 0) {
        continue;
      }
      const [n1, n2, n3] = this.getDigits(x);
      if (n3 > 0) {
        words.push(this.hundreds[n3]);
      }
      if (n2 > 1) {
        words.push(this.twenties[n2]);
      }
      if (n2 == 1) {
        words.push(this.tens[n1]);
      } else if (n1 > 0 && !(index > 0 && x == 1)) {
        words.push(this.ones[n1]);
      }
      if (index > 0) {
        words.push(this.pluralize(x, this.thousands[index]));
      }
    }
    return words.join(' ');
  }
}

/**
 * Converts a value to cardinal (written) form.
 * @param {number|string|bigint} value Number to be convert.
 * @param {object} [options] Options for class.
 * @returns {string} Value in cardinal (written) format.
 * @throws {Error} Value cannot be invalid.
 */
export default function floatToCardinal (value, options = {}) {
  return new N2WordsPL(options).floatToCardinal(value);
}
