import BaseLanguage from '../classes/base-language.js';

export class N2WordsHU extends BaseLanguage {
  constructor(options) {
    options = Object.assign({
      negativeWord: 'mnusz',
      separatorWord: 'egsz',
      zero: 'nulla'
    }, options);

    super(options, [
      [1_000_000_000_000_000_000_000_000_000n, 'quadrillird'],
      [1_000_000_000_000_000_000_000_000n, 'quadrilli'],
      [1_000_000_000_000_000_000_000n, 'trillird'],
      [1_000_000_000_000_000_000n, 'trilli'],
      [1_000_000_000_000_000n, 'billird'],
      [1_000_000_000_000n, 'billi'],
      [1_000_000_000n, 'millird'],
      [1_000_000n, 'milli'],
      [1000n, 'ezer'],
      [100n, 'szz'],
      [90n, 'kilencven'],
      [80n, 'nyolcvan'],
      [70n, 'hetven'],
      [60n, 'hatvan'],
      [50n, 'tven'],
      [40n, 'negyven'],
      [30n, 'harminc'],
      [29n, 'huszonkilenc'],
      [28n, 'huszonnyolc'],
      [27n, 'huszonht'],
      [26n, 'huszonhat'],
      [25n, 'huszont'],
      [24n, 'huszonngy'],
      [23n, 'huszonhrom'],
      [22n, 'huszonkett'],
      [21n, 'huszonegy'],
      [20n, 'hsz'],
      [19n, 'tizenkilenc'],
      [18n, 'tizennyolc'],
      [17n, 'tizenht'],
      [16n, 'tizenhat'],
      [15n, 'tizent'],
      [14n, 'tizenngy'],
      [13n, 'tizenhrom'],
      [12n, 'tizenkett'],
      [11n, 'tizenegy'],
      [10n, 'tz'],
      [9n, 'kilenc'],
      [8n, 'nyolc'],
      [7n, 'ht'],
      [6n, 'hat'],
      [5n, 't'],
      [4n, 'ngy'],
      [3n, 'hrom'],
      [2n, 'kett'],
      [1n, 'egy'],
      [0n, 'nulla']
    ]);
  }

  tensToCardinal(number) {
    if (this.getCardWord(number)) {
      return this.getCardWord(number);
    } else {
      const tens = Math.floor(number / 10);
      const units = number % 10;
      return this.getCardWord(tens * 10) + this.toCardinal(units);
    }
  }

  hundredsToCardinal(number) {
    const hundreds = Math.floor(number / 100);
    let prefix = 'szz';
    if (hundreds != 1) {
      prefix = this.toCardinal(hundreds, '') + prefix;
    }
    const postfix = this.toCardinal(number % 100, '');
    return prefix + postfix;
  }

  thousandsToCardinal(number) {
    const thousands = Math.floor(number / 1000);
    let prefix = 'ezer';
    if (thousands != 1) {
      prefix = this.toCardinal(thousands, '') + prefix;
    }
    let postfix = this.toCardinal(number % 1000, '');
    let middle = (number <= 2000 || postfix == '') ? '' : '-';
    return prefix + middle + postfix;
  }

  bigNumberToCardinal(number) {
    const numberLength = number.toString().length;
    let digits = (numberLength % 3 === 0) ? numberLength - 2 : numberLength;
    let exp = 10 ** (Math.floor(digits / 3) * 3);
    let prefix = this.toCardinal(number / BigInt(exp), '');
    let rest = this.toCardinal(number % BigInt(exp), '');
    let postfix = (rest === '') ? '' : ('-' + rest);
    return prefix + this.getCardWord(exp) + postfix;
  }

  toCardinal(number, zero = this.zero) {
    let words = '';

    if (number == 0n) {
      words = zero;
    } else if (zero == '' && number == 2) {
      words = 'kt';
    } else if (number < 30n) {
      words = this.getCardWord(Number(number));
    } else if (number < 100n) {
      words = this.tensToCardinal(Number(number));
    } else if (number < 1000n) {
      words = this.hundredsToCardinal(Number(number));
    } else if (number < 1_000_000n) {
      words = this.thousandsToCardinal(Number(number));
    } else {
      words = this.bigNumberToCardinal(number);
    }

    return words;
  }
}

/**
 * Converts a value to cardinal (written) form.
 * @param {number|string|bigint} value Number to be convert.
 * @param {object} [options] Options for class.
 * @returns {string} Value in cardinal (written) format.
 * @throws {Error} Value cannot be invalid.
 */
export default function floatToCardinal (value, options = {}) {
  return new N2WordsHU(options).floatToCardinal(value);
}
