import { N2WordsRU } from './ru.js';

export class N2WordsCZ extends N2WordsRU {
  ones = {
    1: 'jedna',
    2: 'dva',
    3: 'ti',
    4: 'tyi',
    5: 'pt',
    6: 'est',
    7: 'sedm',
    8: 'osm',
    9: 'devt'
  };

  tens = {
    0: 'deset',
    1: 'jedenct',
    2: 'dvanct',
    3: 'tinct',
    4: 'trnct',
    5: 'patnct',
    6: 'estnct',
    7: 'sedmnct',
    8: 'osmnct',
    9: 'devatenct'
  };

  twenties = {
    2: 'dvacet',
    3: 'ticet',
    4: 'tyicet',
    5: 'padest',
    6: 'edest',
    7: 'sedmdest',
    8: 'osmdest',
    9: 'devadest'
  };

  hundreds = {
    1: 'sto',
    2: 'dv st',
    3: 'ti sta',
    4: 'tyi sta',
    5: 'pt set',
    6: 'est set',
    7: 'sedm set',
    8: 'osm set',
    9: 'devt set'
  };

  thousands = {
    1: ['tisc', 'tisce', 'tisc'], // 10^ 3
    2: ['milion', 'miliony', 'milion'], // 10^ 6
    3: ['miliarda', 'miliardy', 'miliard'], // 10^ 9
    4: ['bilion', 'biliony', 'bilion'], // 10^ 12
    5: ['biliarda', 'biliardy', 'biliard'], // 10^ 15
    6: ['trilion', 'triliony', 'trilion'], // 10^ 18
    7: ['triliarda', 'triliardy', 'triliard'], // 10^ 21
    8: ['kvadrilion', 'kvadriliony', 'kvadrilion'], // 10^ 24
    9: ['kvadriliarda', 'kvadriliardy', 'kvadriliard'], // 10^ 27
    10: ['quintillion', 'quintilliony', 'quintillion'], // 10^ 30
  };

  constructor(options) {
    options = Object.assign({
      negativeWord: 'mnus',
      zero: 'nula'
    }, options);

    super(options);
  }

  get separatorWord() {
    if (this.wholeNumber == 0n || this.wholeNumber == 1n) {
      return 'cel';
    } else if (this.wholeNumber >= 2n && this.wholeNumber <= 4n) {
      return 'cel';
    } else {
      return 'celch';
    }
  }

  pluralize(n, forms) {
    let form = 2;
    if (n == 1) {
      form = 0;
    } else if (((n % 10n < 5n) && (n % 10n > 1n)) && (n % 100n < 10n || n % 100n > 20n)) {
      form = 1;
    }
    return forms[form];
  }

  toCardinal(number) {
    if (number == 0) {
      return this.zero;
    }
    const words = [];
    const chunks = this.splitByX(number.toString(), 3);
    let index = chunks.length;
    for (const x of chunks) {
      index = index - 1;
      if (x == 0) {
        continue;
      }
      const [n1, n2, n3] = this.getDigits(x);
      if (n3 > 0) {
        words.push(this.hundreds[n3]);
      }
      if (n2 > 1) {
        words.push(this.twenties[n2]);
      }
      if (n2 == 1) {
        words.push(this.tens[n1]);
      } else if (n1 > 0 && !(index > 0 && x == 1)) {
        words.push(this.ones[n1]);
      }
      if (index > 0) {
        words.push(this.pluralize(x, this.thousands[index]));
      }
    }
    return words.join(' ');
  }
}

/**
 * Converts a value to cardinal (written) form.
 * @param {number|string|bigint} value Number to be convert.
 * @param {object} [options] Options for class.
 * @returns {string} Value in cardinal (written) format.
 * @throws {Error} Value cannot be invalid.
 */
export default function floatToCardinal (value, options = {}) {
  return new N2WordsCZ(options).floatToCardinal(value);
}
