import AbstractLanguage from '../classes/abstract-language.js';

export class N2WordsID extends AbstractLanguage {
  base = {
    0: 'khng',
    1: 'mt',
    2: 'hai',
    3: 'ba',
    4: 'bn',
    5: 'nm',
    6: 'su',
    7: 'by',
    8: 'tm',
    9: 'chn',
    10: 'mi',
    11: 'mi mt',
    12: 'mi hai',
    13: 'mi ba',
    14: 'mi bn',
    15: 'mi lm',
    16: 'mi su',
    17: 'mi by',
    18: 'mi tm',
    19: 'mi chn',
  };

  tens = {
    20: 'hai mi',
    30: 'ba mi',
    40: 'bn mi',
    50: 'nm mi',
    60: 'su mi',
    70: 'by mi',
    80: 'tm mi',
    90: 'chn mi',
  };

  thousands = {
    1: 'nghn', // 10^1
    2: 'triu', // 10^2
    3: 't', // 10^3
    4: 'nghn t',
    5: 'trm nghn t',
    6: 'Quintillion',
    7: 'Sextillion',
    8: 'Septillion',
    9: 'Octillion',
    10: 'Nonillion',
    11: 'Decillion',
    12: 'Undecillion',
    13: 'Duodecillion',
    14: 'Tredecillion',
    15: 'Quattuordecillion',
    16: 'Sexdecillion',
    17: 'Septendecillion',
    18: 'Octodecillion',
    19: 'Novemdecillion',
    20: 'Vigintillion',
  };

  constructor(options) {
    super(Object.assign({
      negativeWord: 'm',
      separatorWord: 'phy',
      zero: 'khng'
    }, options));
  }

  convertLess100(number) {
    let unitsPart = number % 10;
    let tensPart = number - unitsPart;
    let tensPartText = this.tens[tensPart];
    if (unitsPart == 0) {
      return tensPartText;
    }
    let unitsPartText = this.base[unitsPart];
    let suffix = unitsPartText;
    if (unitsPart == 1) {
      suffix = 'mt';
    }
    if (unitsPart == 5) {
      suffix = 'lm';
    }
    return tensPartText + ' ' + suffix;
  }

  convertLess1000(number) {
    let words = [];
    let tensUnitsPart = number % 100;
    let hundredsPart = number - tensUnitsPart;
    if (hundredsPart > 0) {
      words.push(this.base[hundredsPart / 100], 'trm');
    }
    if (0 < tensUnitsPart && tensUnitsPart < 10) {
      if (words.length > 0) {
        words.push('l');
      }
      if (tensUnitsPart == 5) {
        words.push('nm');
      } else {
        words.push(this.base[tensUnitsPart]);
      }
    }
    if (tensUnitsPart >= 10) {
      words.push(this.toCardinal(tensUnitsPart));
    }
    return words.join(' ');
  }

  convertMore1000(number) {
    let words = [];
    let division = number / 1000n;
    let power = 1;
    while (division >= 1000n) {
      division = division / 1000n;
      power = power + 1;
    }
    let r = number - (division * BigInt(Math.pow(1000, power)));
    words.push(this.toCardinal(division), this.thousands[power]);
    if (r > 0n) {
      if (r <= 99n) {
        words.push('l');
      }
      words.push(this.toCardinal(r));
    }
    return words.join(' ');
  }

  toCardinal(number) {
    if (number < 20n) {
      return this.base[Number(number)];
    } else {
      if (number < 100n) {
        return this.convertLess100(Number(number));
      } else {
        return (number < 1000n ? this.convertLess1000(Number(number)) : this.convertMore1000(number));
      }
    }
  }
}

/**
 * Converts a value to cardinal (written) form.
 * @param {number|string|bigint} value Number to be convert.
 * @param {object} [options] Options for class.
 * @returns {string} Value in cardinal (written) format.
 * @throws {Error} Value cannot be invalid.
 */
export default function floatToCardinal (value, options = {}) {
  return new N2WordsID(options).floatToCardinal(value);
}
